"use client"

import RequestList from "../../components/RequestList"
import { useEffect } from "react"
import { useAuth } from "../../context/AuthContext"
import { useRouter } from "next/navigation"

export default function ApproverPage() {
  const { isAuthenticated, role, user } = useAuth()
  const router = useRouter()

  useEffect(() => {
    if (!isAuthenticated) {
      router.push("/")
      return
    }
    
    if (role !== "approver") {
      // Jika user bukan approver, redirect ke halaman yang sesuai
      if (role === "peminjam") {
        router.push("/peminjam")
      } else {
        router.push("/")
      }
    }
  }, [isAuthenticated, role, router])

  if (!isAuthenticated || role !== "approver") {
    return (
      <div className="flex items-center justify-center min-h-[60vh]">
        <div className="text-center">
          <div className="w-8 h-8 border-4 border-blue-600 border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-blue-600">Memverifikasi akses...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="space-y-8 bg-gradient-to-br from-blue-50 to-yellow-50 min-h-screen -mx-4 -my-8 px-4 py-8">
      <div className="text-center space-y-4">
        <h1 className="text-3xl font-bold tracking-tight text-blue-900">Dashboard Approver</h1>
        <div className="bg-white rounded-lg p-4 shadow-sm border border-blue-200 max-w-2xl mx-auto">
          <p className="text-blue-700">
            Selamat datang, <span className="font-semibold text-blue-900">{user?.name}</span>! 
            Tinjau dan kelola semua permintaan peminjaman barang dan kendaraan dinas.
          </p>
        </div>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
        <div className="bg-white border border-blue-200 rounded-xl shadow-lg p-6">
          <div className="flex items-center space-x-3">
            <div className="w-12 h-12 bg-yellow-100 rounded-full flex items-center justify-center">
              <div className="w-6 h-6 bg-yellow-500 rounded-full"></div>
            </div>
            <div>
              <p className="text-sm font-medium text-blue-600">Menunggu Review</p>
              <p className="text-3xl font-bold text-blue-900">-</p>
            </div>
          </div>
        </div>

        <div className="bg-white border border-blue-200 rounded-xl shadow-lg p-6">
          <div className="flex items-center space-x-3">
            <div className="w-12 h-12 bg-green-100 rounded-full flex items-center justify-center">
              <div className="w-6 h-6 bg-green-500 rounded-full"></div>
            </div>
            <div>
              <p className="text-sm font-medium text-blue-600">Disetujui</p>
              <p className="text-3xl font-bold text-blue-900">-</p>
            </div>
          </div>
        </div>

        <div className="bg-white border border-blue-200 rounded-xl shadow-lg p-6">
          <div className="flex items-center space-x-3">
            <div className="w-12 h-12 bg-red-100 rounded-full flex items-center justify-center">
              <div className="w-6 h-6 bg-red-500 rounded-full"></div>
            </div>
            <div>
              <p className="text-sm font-medium text-blue-600">Ditolak</p>
              <p className="text-3xl font-bold text-blue-900">-</p>
            </div>
          </div>
        </div>
      </div>

      <div className="bg-white rounded-xl shadow-lg p-6 border border-blue-200">
        <div className="flex items-center justify-between mb-6">
          <h2 className="text-xl font-semibold text-blue-900">Daftar Permintaan</h2>
          <div className="text-sm text-blue-600 bg-blue-50 px-3 py-1 rounded-full">
            Klik tombol "Setujui" atau "Tolak" untuk memproses permintaan
          </div>
        </div>
        <RequestList />
      </div>

      <div className="bg-white rounded-xl shadow-lg p-6 border border-blue-200">
        <h3 className="text-lg font-semibold mb-4 text-blue-900">Panduan Approver</h3>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6 text-sm">
          <div className="space-y-3">
            <h4 className="font-medium text-blue-800">Kriteria Persetujuan:</h4>
            <div className="space-y-2">
              <div className="flex items-start space-x-2">
                <div className="w-2 h-2 bg-green-500 rounded-full mt-2 flex-shrink-0"></div>
                <p className="text-blue-700">Keperluan dinas yang jelas dan valid</p>
              </div>
              <div className="flex items-start space-x-2">
                <div className="w-2 h-2 bg-green-500 rounded-full mt-2 flex-shrink-0"></div>
                <p className="text-blue-700">Tanggal peminjaman tidak bentrok</p>
              </div>
              <div className="flex items-start space-x-2">
                <div className="w-2 h-2 bg-green-500 rounded-full mt-2 flex-shrink-0"></div>
                <p className="text-blue-700">Barang/kendaraan tersedia</p>
              </div>
              <div className="flex items-start space-x-2">
                <div className="w-2 h-2 bg-green-500 rounded-full mt-2 flex-shrink-0"></div>
                <p className="text-blue-700">Peminjam memiliki otoritas yang sesuai</p>
              </div>
            </div>
          </div>
          <div className="space-y-3">
            <h4 className="font-medium text-blue-800">Tindakan yang Diperlukan:</h4>
            <div className="space-y-2">
              <div className="flex items-start space-x-2">
                <div className="w-2 h-2 bg-yellow-500 rounded-full mt-2 flex-shrink-0"></div>
                <p className="text-blue-700">Review setiap permintaan dengan teliti</p>
              </div>
              <div className="flex items-start space-x-2">
                <div className="w-2 h-2 bg-yellow-500 rounded-full mt-2 flex-shrink-0"></div>
                <p className="text-blue-700">Berikan keputusan dalam 1-2 hari kerja</p>
              </div>
              <div className="flex items-start space-x-2">
                <div className="w-2 h-2 bg-yellow-500 rounded-full mt-2 flex-shrink-0"></div>
                <p className="text-blue-700">Dokumentasikan keputusan di Berita Acara</p>
              </div>
              <div className="flex items-start space-x-2">
                <div className="w-2 h-2 bg-yellow-500 rounded-full mt-2 flex-shrink-0"></div>
                <p className="text-blue-700">Koordinasi dengan unit terkait jika diperlukan</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
